@import "./_variables";

html {
  font-size: $base-unit;
}

// Other global styles
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  font-size: 1.6rem;
  color: $text-primary-color;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  list-style-type: none;
}
