@import "../../../assets/scss/styles.scss";

.tableWrapperBlock {
  height: 42.2vh;
  overflow: auto;

  table {
    font-size: 1.4rem;
    line-height: 2rem;
    width: 100%;
    th {
      font-weight: 500;
      width: 25%;
      background-color: $primary-color;
      color: $text-secondary-color;
      padding: 1rem 1.2rem;

      &:first-child {
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
      }
      &:last-child {
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
      }
    }
    td {
      padding: 1.4rem 1.2rem;
      font-weight: 400;
      color: $text-primary-color;
      border-bottom: 1px solid $secondary-color;
    }
    & .orderId {
      color: $link-text;
      text-align: left;
    }

    .orderDateHead {
      cursor: pointer;
      svg {
        position: relative;
        top: -2px;
      }
    }

    .orderIdHead,
    .orderDateHead,
    .orderDate {
      text-align: left;
    }

    .orderAmount,
    .orderAmountHead,
    .orderFeesHead,
    .orderFees {
      text-align: right;
    }

    .orderFeesHead {
      img {
        position: relative;
        top: 3px;
      }
    }
  }
}
